<template>
	<view class="board">
		<view class="box">
			<view class="title">编辑项目</view>
			<view class="handle_area flex flex_btween" style="align-items: start;">
				<view class="sub_box">
					<view class="line">
						<view class="tit">选择合同</view>
						<view class="input_wrap">
							<view class="work_type">
								<view class="work" @click="toChooseContract">
									{{!currentInfo.contract_template_name?"不需要合同":currentInfo.contract_template_name}}
								</view>
								<view class="work_wrap contract_wrap" v-if="showContract">
									<view class="item flex flex_btween" @click="dontNeedContract">不需要合同</view>
									<view class="item" v-for="(item,index) in list" :key="index"
										@click="chooseContract(item)">{{item.sign_template_name}}</view>
								</view>
							</view>
						</view>
					</view>
					<!-- <view class="line">
						<view class="tit">服务类别</view>
						<view class="input_wrap">
							<view class="work_type">
								<view class="work" @click="toChooseType">{{currentInfo.work_type_name}}</view>
								<view class="work_wrap" v-if="showWorkType">
									<workType @operation="getWorkType"></workType>
								</view>
							</view>
						</view>
					</view> -->
					<view class="line">
						<view class="tit">选择保险</view>
						<view class="input_wrap">
							<view class="work_type">
								<view class="work" @click="toChooseInsure">
									{{!currentInfo.insurance_meal_name?"不需要保险":currentInfo.insurance_meal_name}}
								</view>
								<view class="work_wrap contract_wrap" v-if="showInsure">
									<view class="item flex flex_btween" @click="dontNeed">不需要保险</view>
									<view class="item flex flex_btween" v-for="(item,index) in insureList" :key="index"
										@click="chooseInsure(item)">
										<view class="">{{item.meal_name}}</view>
										<view class="">{{item.cost_price + "/天；"+item.month_price + "/月"}}</view>
									</view>
								</view>
							</view>
						</view>
					</view>
					<view class="line">
						<view class="tit">项目名称</view>
						<view class="input_wrap">
							<input type="text" v-model="currentInfo.name" placeholder="请输入用工方" maxlength="32" />
						</view>
					</view>
					<view class="line">
						<view class="tit">用工方</view>
						<view class="input_wrap">
							<input type="text" v-model="currentInfo.employer_name" placeholder="请输入用工方" />
						</view>
					</view>
					<view class="line">
						<view class="tit">是否显示用工方</view>
						<view class="input_wrap noborder">
							<radio-group @change="radioChange" class="flex flex-start">
								<label class="uni-list-cell uni-list-cell-pd flex flex-start"
									v-for="(item, index) in items" :key="item.value" style="margin-right: 15px;">
									<view>
										<radio :value="item.value" :checked="index === currentRadio" />
									</view>
									<view>{{item.name}}</view>
								</label>
							</radio-group>
						</view>
					</view>
					<view class="line">
						<view class="tit">用工人数</view>
						<view class="input_wrap">
							<input type="number" v-model="currentInfo.need_worker_num" placeholder="请输入用工人数" />
						</view>
					</view>
					<view class="line">
						<view class="tit">项目地址</view>
						<view class="input_wrap">
							<input type="text" v-model="currentInfo.work_address" placeholder="请输入工作地址" />
						</view>
					</view>

				</view>
				<view class="sub_box">
					<view class="line">
						<view class="tit">服务时间</view>
						<view class="input_wrap flex flex_start noborder">
							<uni-datetime-picker type="datetime" :value="currentInfo.work_start_time"
								@change="chengeStartTime" />
							<view style="margin:0 20px;">—</view>
							<uni-datetime-picker type="datetime" :value="currentInfo.work_end_time"
								@change="chengeEndTime" />
						</view>
					</view>
					<view class="line">
						<view class="tit">签到时间</view>
						<view class="input_wrap noborder">
							<uni-datetime-picker type="datetime" :value="currentInfo.checkin_time"
								@change="chengeCheckInTime" />
						</view>
					</view>
					<view class="line">
						<view class="tit">到岗时间</view>
						<view class="input_wrap noborder">
							<uni-datetime-picker type="datetime" :value="currentInfo.arrival_time"
								@change="chengeArriveTime" />
						</view>
					</view>
					<view class="line">
						<view class="tit">服务费用</view>
						<view class="input_wrap flex flex-start">
							<input type="text" v-model="currentInfo.salary_unit_price" placeholder="请输入薪资" />
							<view class="periods flex flex-start">
								<view class="option flex flex-start" v-for="(item,index) in periodList" :key="index"
									@click="choosePeriod(index)">
									<image
										:src="curWorkTimePeriodIndex == index?'/static/checked.png':'/static/uncheck.png'"
										mode="widthFix"></image>
									<view class="text">/</view>
									<view class="text">{{item.text}}</view>
								</view>
							</view>
						</view>
					</view>
					<view class="line">
						<view class="tit">结算方式</view>
						<view class="input_wrap flex flex-start noborder">
							<!-- <input type="text" v-model="workTime" placeholder="请输入工作时长"/> -->
							<view class="periods flex flex-start">
								<view class="option flex flex-start" v-for="(item,index) in periodList" :key="index"
									@click="choosePeriod(index)">
									<image
										:src="curWorkTimePeriodIndex == index?'/static/checked.png':'/static/uncheck.png'"
										mode="widthFix"></image>
									<view class="text">{{item.text}}</view>
								</view>
							</view>
						</view>
					</view>
					<view class="line">
						<view class="tit">发薪周期</view>
						<view class="input_wrap flex flex-start noborder">
							<!-- <input type="text" v-model="workTime" placeholder="请输入工作时长"/> -->
							<view class="periods flex flex-start">
								<view class="option flex flex-start" v-for="(item,index) in salaryPeriodList"
									:key="index" @click="chooseSalaryPeriod(index)">
									<image
										:src="curSalaryPeriodIndex == index?'/static/checked.png':'/static/uncheck.png'"
										mode="widthFix"></image>
									<view class="text">{{item.text}}</view>
								</view>
							</view>
							<view class="more flex flex_start">
								<view class="subt">发薪时间：</view>
								<picker mode="time" @change="changeSalayTime" v-if="curSalaryPeriodIndex == 0">
									<view>{{currentInfo.pay_salary_setting.daily}}</view>
								</picker>
								<uni-data-select v-model="currentInfo.pay_salary_setting.weekly" :localdata="weekRange"
									@change="weekChange" v-if="curSalaryPeriodIndex == 1"></uni-data-select>
								<view class="month flex flex-start" v-if="curSalaryPeriodIndex == 2">
									<input type="text" v-model="currentInfo.pay_salary_setting.monthly" />
									<text>号</text>
								</view>
							</view>
						</view>
					</view>
					<view class="line">
						<view class="tit">项目描述</view>
						<view class="input_wrap noborder" style="height:auto;">
							<textarea v-model="currentInfo.content" placeholder="请输入详细需求" maxlength="500" />
						</view>
					</view>

					<view class="line">
						<view class="btn" @click="submit">确认修改</view>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	function getToday() {
		let date = new Date();
		let year = date.getFullYear();
		let month = date.getMonth() + 1;
		let day = date.getDate();
		if (month < 10) {
			month = "0" + month
		}
		if (day < 10) {
			day = "0" + day
		}
		return year + "-" + month + "-" + day
	}
	export default {
		name: "add_require",
		data() {
			return {

				projectName: "",
				name: "",
				servStartTime: getToday(),
				servEndTime: getToday(),
				checkInTime: getToday(),
				arriveTime: getToday(),
				status: "draft",
				content: "",
				count: "",
				workTime: "",
				salary: "",
				profession: "",
				location: "",
				figure: "",
				minAge: "",
				maxAge: "",
				minHeight: "",
				maxHeight: "",
				postName: "",
				workType: {
					name: "请选择"
				},
				curContract: {
					name: "请选择"
				},
				endTime: getToday(),
				address: "",
				periodList: [{
						value: "hour",
						text: "时"
					},
					{
						value: "day",
						text: "日"
					},
					{
						value: "week",
						text: "周"
					},
					{
						value: "month",
						text: "月"
					},
				],
				salaryPeriodList: [{
						value: "daily",
						text: "日"
					},
					{
						value: "weekly",
						text: "周"
					},
					{
						value: "monthly",
						text: "月"
					},
				],
				curWorkTimePeriodIndex: 0,
				curSalaryPeriodIndex: 0,
				work_duration_period: "",
				currentTabIndex: 0,
				salary_period: "",
				showWorkType: false,
				showContract: false,
				list: [],
				showInsure: false,
				curInsure: {},
				insureList: [],
				salaryTime: "18:00",
				weekValue: 0,
				weekRange: [{
						value: "0",
						text: "星期一"
					},
					{
						value: "1",
						text: "星期二"
					},
					{
						value: "2",
						text: "星期三"
					},
					{
						value: "3",
						text: "星期四"
					},
					{
						value: "4",
						text: "星期五"
					},
					{
						value: "5",
						text: "星期六"
					},
					{
						value: "6",
						text: "星期日"
					}
				],
				is_employer_name_public: false,
				currentRadio: 1,
				items: [{
						value: true,
						name: "展示"
					},
					{
						value: false,
						name: "隐藏"
					},
				],
				monthValue: 15,
				id: "",
				currentInfo: {
					"broker_company_name": "",
					"content": "",
					"contracted_num": 0,
					"employer_name": "",
					"arrival_time": "",
					"checkin_time": "",
					"id": 0,
					"is_employer_name_public": false,
					"name": "",
					"need_worker_num": 1,
					"pay_salary_setting": {
						"type": "",
						"daily": "",
						"weekly": "",
						"monthly": ""
					},
					"salary_settlement_type": "",
					"salary_unit_price": "",
					"work_address": "",
					"work_end_time": "",
					"work_start_time": "",
					"work_type": "",
					"work_type_code": "",
					"work_type_id": 0,
					"work_type_name": ""
				},
			};
		},
		components: {},
		created() {
			// this.getInsureList()
			this.getContractList()
			let paramArr = []
			if (location.href.indexOf("?") != -1) {
				paramArr = location.href.split("?")[1].split("&")
			}
			for (var i = 0; i < paramArr.length; i++) {
				if (paramArr[i].indexOf("id") != -1) {
					this.id = paramArr[i].split("=")[1]
				}
			}
			this.getInfo()

		},
		methods: {
			radioChange(e) {
				this.is_employer_name_public = e.detail.value
				this.currentInfo.is_employer_name_public = e.detail.value
			},
			chengeStartTime(e) {
				this.currentInfo.work_start_time = e.slice(0, 16)
			},
			chengeEndTime(e) {
				this.currentInfo.work_end_time = e.slice(0, 16)
			},
			chengeCheckInTime(e) {
				this.currentInfo.checkin_time = e.slice(0, 16)
			},
			chengeArriveTime(e) {
				this.currentInfo.arrival_time = e.slice(0, 16)
			},
			choosePeriod(index) {
				this.curWorkTimePeriodIndex = index
				this.currentInfo.salary_settlement_type = this.periodList[index].value
			},
			chooseSalaryPeriod(index) {
				this.curSalaryPeriodIndex = index
				switch (index) {
					case 0:
						this.currentInfo.pay_salary_setting.type = "daily"
						this.currentInfo.pay_salary_setting.weekly = ""
						this.currentInfo.pay_salary_setting.monthly = ""
						break;
					case 1:
						this.currentInfo.pay_salary_setting.type = "weekly"
						this.currentInfo.pay_salary_setting.monthly = ""
						this.currentInfo.pay_salary_setting.daily = ""
						break;
					case 2:
						this.currentInfo.pay_salary_setting.type = "monthly"
						this.currentInfo.pay_salary_setting.daily = ""
						this.currentInfo.pay_salary_setting.weekly = ""
						break;
					default:
						break;
				}

			},
			changeTab(index) {
				this.currentTabIndex = index
			},
			changeSalayTime(e) {
				this.currentInfo.pay_salary_setting.daily = e.detail.value
			},
			weekChange(e) {
				console.log(e)
			},
			toChooseType() {
				this.showWorkType = !this.showWorkType
			},
			toChooseContract() {
				this.showContract = !this.showContract
			},
			toChooseInsure() {
				this.showInsure = !this.showInsure
			},
			getWorkType(e) {
				console.log(e)
				this.showWorkType = false
				this.currentInfo.work_type_name = e.name
				this.currentInfo.work_type_id = e.id
				this.currentInfo.work_type = e.type
				this.getInsureList(e.type)
			},
			chooseContract(item) {
				this.showContract = false
				this.$set(this.currentInfo, 'contract_template_id', item.sign_template_id)
				this.$set(this.currentInfo, 'contract_template_name', item.sign_template_name)
			},
			chooseInsure(item) {
				console.log(item, 'item')
				this.showInsure = false
				this.$set(this.currentInfo, 'insurance_meal_id', item.id)
				this.$set(this.currentInfo, 'insurance_meal_name', item.meal_name)
			},
			dontNeedContract() {
				this.$set(this.currentInfo, 'contract_template_id', "")
				this.$set(this.currentInfo, 'contract_template_name', "")
				this.showContract = false
			},
			dontNeed() {
				this.showInsure = false
				this.$set(this.currentInfo, 'insurance_meal_id', "")
				this.$set(this.currentInfo, 'insurance_meal_name', "")
			},
			chengeEndTime(e) {
				this.endTime = e.detail.value
			},
			getContractList() {
				let url = "/broker/contract/sign-templates"
				this.$request(url).then(res => {
					if (res.code == 0) {
						this.list = res.data.list
					}
				})
			},
			getInsureList(type) {
				let url = "/broker/insurance-meals?work_type=" + type
				this.$request(url).then(res => {
					if (res.code == 0) {
						this.insureList = res.data.list
					}
				})
			},
			getInfo() {
				let url = "/broker/project/" + this.id
				this.$request(url).then(res => {
					if (res.code == 0) {
						this.currentInfo = res.data
						this.getInsureList(this.currentInfo.work_type)
						this.is_employer_name_public = res.data.is_employer_name_public
						if (res.data.is_employer_name_public) {
							this.currentRadio = 0
						} else {
							this.currentRadio = 1
						}
						for (var j = 0; j < this.periodList.length; j++) {
							if (this.periodList[j].value == this.currentInfo.salary_settlement_type) {
								this.curWorkTimePeriodIndex = j
								console.log(this.curWorkTimePeriodIndex)
							}
						}
						console.log(this.currentInfo.pay_salary_setting.type)
						for (var k = 0; k < this.salaryPeriodList.length; k++) {
							if (this.salaryPeriodList[k].value == this.currentInfo.pay_salary_setting.type) {
								this.curSalaryPeriodIndex = k
							}
						}
						console.log(this.curWorkTimePeriodIndex)
					}
				})
			},
			submit() {
				console.log(this.currentInfo)
				let url = "/broker/project/" + this.id + "/update"
				this.currentInfo.need_worker_num = Number(this.currentInfo.need_worker_num)
				this.$request(url, this.currentInfo, "POST").then(res => {
					if (res.code == 0) {
						uni.showToast({
							title: "项目信息更新成功",
							duration: 200
						})
						this.setPageName({
							pageName: "project_list"
						}, "", "all")
						this.$emit("getPageName", {
							pageName: "project_list"
						})
					}
				})

			}
		}
	}
</script>

<style lang="scss">
	::v-deep {
		uni-picker {
			width: 100%;
			padding: 0 15px;
			height: 30px;
			line-height: 30px;
			border: 1px solid #e9e9e9;
			border-radius: 5px;
			margin-right: 20px;
		}

		.uni-select {
			height: 30px;
			font-size: 14px;
		}
	}

	.box {
		.sub_box {
			width: 48%;
		}

		.line {
			margin-bottom: 20px;

			&.scope {
				align-items: start;
			}

			.tit {
				color: rgba(0, 0, 0, 0.85);
				line-height: 22px;
			}

			.input_wrap {
				width: 460px;
				height: 30px;
				border-radius: 2px;
				position: relative;
				border: 1px solid #D9D9D9;
				margin-top: 8px;

				.more {
					.subt {
						white-space: nowrap;
						color: #f47106;
					}

					uni-picker {
						color: #f47106;
					}

					.month {
						input {
							width: 40px;
							height: 30px;
							text-align: center;
							border: 1px solid #ccc;
							padding: 0;
							border-radius: 4px;
							margin-right: 5px;
							color: #f47106;
						}
					}
				}

				&.age {
					input {
						border: 1px solid #e9e9e9;
						width: 140px;
						border-radius: 5px;
					}

					text {
						display: inline-block;
						margin: 0 10px;
					}
				}

				.work_type {
					position: relative;

					.work {
						height: 30px;
						line-height: 30px;
						padding-left: 15px;
						cursor: pointer;
						font-size: 14px;
					}

					.work_wrap {
						min-width: 100%;
						position: absolute;
						top: 100%;
						left: 0;
						z-index: 99;
						background: #fff;
						box-shadow: 0 0 5px 5px #e8e8e8;
						padding: 10px;
						box-sizing: border-box;
						height: 300px;
						overflow: scroll;
					}

					.contract_wrap {
						height: 110px;
						padding: 0;

						.item {
							font-size: 14px;
							height: 30px;
							line-height: 30px;
							padding: 0 10px;
							box-sizing: border-box;

							&:hover {
								cursor: pointer;
								background: #e8e8e8;
								color: $base-color;
							}
						}
					}
				}

				.abso {
					position: absolute;
					top: 50%;
					right: 15px;
					transform: translateY(-50%);
				}

				&.noborder {
					border: none;
				}

				input {
					width: 100%;
					height: 30px;
					padding-left: 15px;
					font-size: 14px;
				}

				.option {
					margin-right: 20px;
					cursor: pointer;

					image {
						width: 20px;
						height: 20px;
						margin-right: 5px;
					}
				}

				.long {
					image {
						width: 20px;
						height: 20px;
						margin-right: 5px;
						cursor: pointer;
					}
				}

				textarea {
					width: 100%;
					padding: 15px;
					min-height: 30px;
					max-height: 100px;
					border: 1px solid #D9D9D9;
					font-size: 14px;
					overflow-y: scroll;
				}
			}
		}
	}
</style>